.app {
    max-width: 600px;
    margin: 50px auto;
}

.search {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.list {
    list-style: none;
    padding: 0;
}

.listItem {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
    cursor: pointer;
}

.listItem:hover {
    background-color: #e0e0e0;
}

.definition {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f1f1f1;
    position: relative;
    z-index: 10;
}
